import React, { useEffect, useState } from 'react'
import AdminNav from './AdminNav'
import Lottie from 'lottie-react'
import { useNavigate, useParams } from 'react-router-dom'
import axiosInstance from '../baseUrl';
import { toast } from 'react-toastify';
import img from './Assests/editPrgm.json'

function AdminEditPrgm() {

    const {id}=useParams();
    console.log(id);

    const [value,setValue] = useState({});
    const navigate=useNavigate();  
  
    useEffect(() => {
  
  
      axiosInstance.post(`/viewEventById/${id}`)
      .then((res)=>{
      console.log(res);
      setValue(res.data.data)
      
    })
    }, []);
  
    const updatefcn=(e)=>{
      e.preventDefault();
  
      axiosInstance.post(`/editevent/${id}`,value)
      .then((response)=>{
        console.log(response);
        if (response.data.status==200) {
          toast.success('Updated')
          navigate('/admin_view_program')
        }
  
      })
      .catch((err)=>{
        console.log(err);
      })
    }
  
    const changefn = (e)=>{
  setValue({...value, [e.target.name]:e.target.value})
    }
  
    useEffect(()=>{
      console.log(value);
    })



  return (
    <div>
      <AdminNav/>
      <div class="container-fluid bg-light mt-5 p-5">
        <div class="container">
            <div class="row align-items-center">
                <div class="col-lg-5">
                    <img class="img-fluid w-100" src="img/feature.jpg" alt=""/>
                    <Lottie animationData={img} />
                </div>
                <div class="col-lg-7 py-5 py-lg-0 px-3 px-lg-5">
                    <div style={{padding: '30px', background: '#f6f6f6'}}>
                    <h3 class="mb-4">Edit Program</h3>
                    <form onSubmit={updatefcn} >
                        <div class="form-group">
                            <label for="name">Program Name *</label>
                            <input type="text" class="form-control" value={value.eventname} onChange={changefn} name='eventname' />
                        </div>
                        <div class="form-group">
                            <label for="email">Category *</label>
                            <input type="text" class="form-control" value={value.category} onChange={changefn} name='category' />
                        </div>
                        <div class="form-group">
                            <label for="name">Duration *</label>
                            <input type="number" class="form-control" value={value.duration} onChange={changefn} name='duration' />
                        </div>
                        
                       
                        <div class="form-group mb-0">
                            <button type="submit"  class="btn btn-primary px-3" >Update</button>
                        </div>
                    </form>
                </div>
                </div>
            </div>
        </div>
    </div>
    </div>
  )
}

export default AdminEditPrgm
